.stats-container {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  margin-top: 10px;
  background-color: rgba(193, 153, 225, 0.656);
  border-radius: 15px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.rate-section, .budget-section {
  flex: 1;
  margin: 0 10px;
}

.rate-bar-container {
  margin-bottom: 10px;
}

.rate-bar-container span {
  margin-right: 10px;
}

.rate-bar-background {
  background-color: #ddd; /* 背景色を薄い灰色に設定 */
  width: 100%; /* 背景の長さを最大に */
  height: 20px;
}

.rate-bar {
  height: 100%;
  transition: width 0.5s ease; /* 棒グラフの幅の変更にアニメーションを追加 */
}

.budget-bar-container img {
  max-width: 100%; /* 画像がコンテナを超えないように */
  height: auto; /* アスペクト比を保持 */
  transition: width 0.5s ease; /* サイズ変更のアニメーション */
}

/* 追加のスタイルが必要な場合はここに書く */
/* スマートフォンサイズ (480px以下) のスタイル */
@media (max-width: 480px) {
  .stats-container {
    flex-direction: column; /* 縦方向にスタック */
    align-items: center; /* 中央揃え */
    padding: 5px;
    margin-top: 5px;
    background-color: rgba(193, 153, 225, 0.656); /* 背景色を設定 */
    border-radius: 15px; /* 角を丸く */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 影をつける */
    width: 100%; /* 幅を最大に */
    /*max-height: 20vh; /* 最大高さをビューポートの20%に */
  }

  .rate-section, .budget-section {
    flex: none; /* フレックスアイテムとしての挙動をリセット */
    flex-basis: 100%; /* 子要素を幅いっぱいに */
    margin: 5px 0; /* 上下の余白を設定 */
  }

  .rate-bar-container, .budget-bar-container {
    width: 90%; /* スマートフォンでは幅を最大に */
  }

  .rate-bar-background, .budget-bar-container {
    width: 90%; /* バーコンテナの幅を調整 */
    margin: 0 auto; /* 横方向の中央揃え */
  }
  
  .rate-bar-background, .rate-bar, .budget-bar-container img {
    height: 10px; /* 棒グラフの高さを減らす */
  }

  .budget-bar-container img {
    width: auto; /* 画像の幅を自動にして高さに合わせる */
    max-width: 100%; /* 画像がコンテナを超えないように */
    height: 10px; /* 画像の高さを調整 */
  }

  .rate-bar-container span, .budget-section span {
    font-size: 10px; /* 文字サイズを小さく */
  }
}